import data from '../delegate';
import ScrollAnimation from 'react-animate-on-scroll';


export default function GridList() {
    const grid = data.gridlist.items
    return <div >




        <h2 className="center">Our Services</h2>

        <div className='container'>
            At Nu-Birth home services, it is our mission to build new relationships with families while providing high quality services at affordable rates that exceeds industry standards.

            {grid && grid.map((p, i) =>
                <div key={p.title} className='card deep-purple lighten-3'>
                    <div className='card-content'>                <ScrollAnimation animateIn="fadeIn" duration={3000}>
                        <span className='card-title center'><h4>{p.title}</h4></span>
                        <div className='section row fade-item'>

                            <div className={i % 2 === 0 ? "col s12 m6 l7" : "col s12 m6 push-m6 l5 push-l7 center"}>
                                <img alt={p.title} className="responsive-img" src={p.img} />
                            </div>
                            <div className={i % 2 === 0 ? "col s12 m6 l5 center" : "col s12 m6 pull-m6 pull-l4 l5 center "}>

                                <p >{p.description}</p>
                            </div>

                        </div>
                        <div className='small-container'>
                            <a className="modal-trigger " href="#contactForm" className='modal-trigger btn btn-block'>More info</a>
                        </div>
                    </ScrollAnimation>
                    </div>
                </div>
            )}



        </div>
    </div>
};

